@import "../../global.scss";
.intro{
    background-color: white;
    display:flex;
    
      @include mobile{
        flex-direction: column;
        align-items: center;
        }

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 850px;
            height: 850px;
            background-color: red;
            border-radius: 30%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
            }
           
            img{
                height: 100%;

                @include mobile{
                    height: 50%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-right: 80%;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;
                
                @include mobile{
                    font-size: 30px;
               
            }
        }
            h2{
                font-size: 40px;
            }
            h3{
                font-size: 30px;

                @include mobile{
                    font-size: 15px;
               
            }

                

                span{
                    font-size: inherit;
                    color: crimson;
                }
                .typed-cursor{
                    animation:blink 1s infinte;
                }
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                    
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;
            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
                
            }
        }
        

    }
    }
