@import "../../global.scss";
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
   
    

    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
        
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            flex-wrap: wrap;
            justify-content: center;
            margin: 10px;
        }
    }
    .container{
        
        width: 70%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;

        @include mobile{
            width: 100%;
        }
        

        .item{
            width: 300px;
            height: 250px;
            border-radius: 10px;
            border: 1px solid lightgrey;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            overflow: hidden;
            transition: all 1s ease;
            cursor: pointer;
            @include mobile{
                width: 130px;
                height: 110px;
            }
        
        h3{
            position: absolute;
            font-size: 20px;
            
        }
        img{
            width: 150%;
            height: 150%;
            object-fit:cover;
            z-index: 1;
        }
        &:hover{
            background-color: $mainColor ;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
    }
        
    }
}
